/**
 * @description: 页面与主题有关颜色配置文件
 * @description: 财大版本
 * @param {type} 
 * @return: 
 */
.operation-link {
  color: #24695C;
  cursor: pointer;
}
.costom-menu-theme {
  background: #24695C;
}
.costom-menu-theme .logo {
  background: #C08E60;
}
.costom-menu-theme .logo span {
  color: #fff;
}
.ant-menu-dark {
  background: #24695C !important;
}
.ant-menu-dark .ant-menu-item {
  color: rgba(255, 255, 255, 0.75);
}
.ant-menu-dark .ant-menu-item:hover {
  background: #1A5B4F !important;
}
.ant-menu-dark .ant-menu-item .anticon {
  font-size: 24px !important;
}
.ant-menu-dark .ant-menu-sub {
  background: #24695C !important;
}
.ant-menu-dark .ant-menu-inline.ant-menu-sub {
  background: rgba(0, 0, 0, 0.15) !important;
  box-shadow: none !important;
}
.ant-menu-dark .ant-menu-item-selected .anticon {
  color: #ffffff !important;
}
.ant-menu-dark .ant-menu-item-selected::before {
  background: #C08E60;
}
.ant-menu-dark .ant-menu-item,
.ant-menu-dark .ant-menu-submenu > .ant-menu-submenu-title {
  color: rgba(255, 255, 255, 0.75);
}
.ant-menu-dark .ant-menu-item:hover,
.ant-menu-dark .ant-menu-submenu > .ant-menu-submenu-title:hover {
  color: #ffffff;
}
.ant-menu-dark .ant-menu-item.ant-menu-item-selected,
.ant-menu-dark .ant-menu-submenu > .ant-menu-submenu-title.ant-menu-item-selected {
  background: #1A5B4F !important;
}
.ant-menu-dark .ant-menu-item.ant-menu-item-selected span,
.ant-menu-dark .ant-menu-submenu > .ant-menu-submenu-title.ant-menu-item-selected span {
  color: #ffffff !important;
}
body {
  margin: 0;
  font-family: -apple-system, "cursive", BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body #root {
  min-height: 100%;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
* {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}
::-webkit-scrollbar {
  width: 8px;
  height: 4px;
}
::-webkit-scrollbar-thumb {
  background-color: #CCCCCC;
  height: 40px;
  outline-offset: -1px;
  outline: 1px solid #CCCCCC;
  -webkit-border-radius: 2px;
  border: 1px solid #CCCCCC;
}
table {
  table-layout: fixed;
}
table td {
  text-overflow: ellipsis !important;
  white-space: nowrap !important;
  overflow: hidden !important;
}
.ant-table-thead > tr > th,
.ant-table-tbody > tr > td {
  padding: 10px 16px !important;
}
.ant-table-thead > tr.ant-table-row-hover:not(.ant-table-expanded-row):not(.ant-table-row-selected) > td,
.ant-table-tbody > tr.ant-table-row-hover:not(.ant-table-expanded-row):not(.ant-table-row-selected) > td,
.ant-table-thead > tr:hover:not(.ant-table-expanded-row):not(.ant-table-row-selected) > td,
.ant-table-tbody > tr:hover:not(.ant-table-expanded-row):not(.ant-table-row-selected) > td {
  background: #F6FBFB !important;
}
.anchorBL {
  display: none !important;
}
.ant-pagination-total-text {
  float: right !important;
  margin-left: 15px;
  line-height: 32px;
}
.iconfont {
  margin-right: 10px;
}
